import styled, { css } from "styled-components";
import { LazyLoadImage } from "react-lazy-load-image-component";
import { InnerWrap, Text, Title1 } from "../../assets/styles/component";

import Logo_desktop from "../../assets/images/logo_desktop.svg";
import telegram from "../../assets/images/icon_telegram.svg";
import vk from "../../assets/images/icon_vk.svg";
import instagram from "../../assets/images/icon_instagram.svg";
import odnoklassniki from "../../assets/images/icon_odnoklassniki.svg";
import yandexzen from "../../assets/images/icon_yandexzen.svg";
import youtube from "../../assets/images/icon_youtube.svg";

const SFooter = styled.footer`
  position: relative;
  overflow: hidden;
  padding-top: 100px;

  background: radial-gradient(
      30.3% 30.3% at 40.28% 69.7%,
      rgba(241, 2, 117, 0.68) 0%,
      rgba(241, 2, 117, 0) 100%
    ),
    radial-gradient(
      24.23% 30.52% at 74.88% 74.55%,
      rgba(255, 66, 158, 0.67) 0%,
      rgba(255, 66, 158, 0) 100%
    ),
    radial-gradient(
      44.55% 37.99% at 58% 75.77%,
      rgba(255, 66, 158, 0.68) 0%,
      rgba(255, 66, 158, 0) 100%
    ),
    radial-gradient(
      27.38% 33.43% at 23.42% 72.62%,
      rgba(2, 2, 241, 0.59) 0%,
      rgba(2, 2, 241, 0) 100%
    );

  & > div {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;

    & > p {
      text-align: center;
      margin-bottom: 50px;
    }
  }

  @media (max-width: 1200px) {
    padding: 70px 0 0 0;
  }
`;

const Title = styled(Title1)`
  text-align: center;
  margin-bottom: 90px;
`;

const BlueBG = styled.span`
  background: radial-gradient(
      81.57% 98.97% at -3.3% -14.6%,
      #ecbbc3 0%,
      rgba(200, 62, 198, 0.637795) 57.81%,
      rgba(160, 9, 230, 0) 100%
    ),
    #0202f1;
  box-shadow: inset 0px 0px 43px rgba(255, 255, 255, 0.25);
  border-radius: 63.5px;
  padding: 1px 45px 6px 44px;
`;

const PinkBG = styled.span`
  position: relative;
  padding: 0px 46px 10px 40px;
  border-radius: 103px;
  border-top-left-radius: 0;
  background: radial-gradient(
      123.91% 117.38% at 111.7% 118.95%,
      #ecbbc3 0%,
      rgba(200, 62, 198, 0.637795) 57.81%,
      rgba(160, 9, 230, 0) 100%
    ),
    #cf2e82;
  box-shadow: inset 0px 0px 43px rgba(255, 255, 255, 0.25);

  svg {
    position: absolute;
    top: -40px;
    left: 0;

    @media (max-width: 1000px) {
      width: 30px;
      top: -29px;
    }
  }
`;

const Button = styled.button`
  border: 0;
  cursor: pointer;
  font-weight: 400;
  font-size: clamp(10px, 3vw, 26px);
  line-height: 120%;
  text-align: center;
  letter-spacing: 0.01em;
  color: #29009c;
  background: #ffffff;
  outline: 10px solid rgba(255, 255, 255, 0.1);
  border-radius: 73px;
  padding: 37px 96px;
  margin-bottom: 190px;
  transition: 0.25s ease-in-out;

  &:hover {
    transform: scale(1.01);
    outline: 14px solid rgba(255, 255, 255, 0.1);
  }

  @media (max-width: 1300px) {
    padding: 27px 60px;
    margin-bottom: 120px;
  }

  @media (max-width: 1000px) {
    padding: 23px 60px;
    margin-bottom: 70px;
  }
`;

const FooterNav = styled.div`
  background: #050816;
  border-top-left-radius: 106px;
  border-top-right-radius: 106px;
  width: 100%;
  padding: 116px 0 110px 0;
  box-sizing: border-box;

  & > div {
    width: 82%;
    display: grid;
    grid-template-columns: 1fr 1.4fr 1fr 1fr;
    grid-template-rows: 1fr 24px;
    gap: 119px;

    & > p {
      font-weight: 400;
      font-size: 16px;
      line-height: 150%;
    }
  }

  @media (max-width: 1350px) {
    & > div {
      grid-template-columns: 1fr 1fr 1fr 150px;
      grid-template-rows: 1fr;
      gap: 50px;
    }
  }

  @media (max-width: 900px) {
    & > div {
      grid-template-columns: 1fr;
      grid-template-rows: 1fr;
      gap: 50px;
    }
  }
`;

const LogoImg = styled(LazyLoadImage)``;

const Nav = styled.div`
  display: flex;
  flex-direction: column;
  gap: 20px;

  a {
    color: #fff;
  }

  @media (max-width: 900px) {
    display: grid;
    grid-template-columns: 1fr 1fr;
  }
`;

const Contacts = styled.div`
  display: flex;
  flex-direction: column;

  p {
    margin-bottom: 26px;
  }

  a {
    color: #fff;
    font-size: 18px;
  }
`;

const Email = styled.a`
  margin-top: 12px;
  background: radial-gradient(
      152.32% 158.02% at -13.45% 12.49%,
      #d83575 3.12%,
      #7e03f4 100%
    ),
    #752bdd;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  text-fill-color: transparent;
`;

const Socials = styled.div`
  display: flex;
  gap: 17px;

  img {
    cursor: pointer;
    width: 30px;
    height: 30px;
    aspect-ratio: 30/30;
    transition: 0.2s ease-in-out;
    &:hover {
      filter: brightness(1.1);
      transform: scale(1.1);
    }
  }

  @media (max-width: 1350px) {
    flex-wrap: wrap;
  }
`;

const Policy = styled.a`
  background: radial-gradient(
      152.32% 158.02% at -13.45% 12.49%,
      #d83575 3.12%,
      #7e03f4 100%
    ),
    #752bdd;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  text-fill-color: transparent;

  span {
    background: linear-gradient(90.41deg, #b421a8 -1.51%, #900ddc 103.56%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    text-fill-color: transparent;
  }
`;

const Footer = ({ language }) => {
  return (
    <SFooter>
      <InnerWrap>
        <Title>
          Давайте начнем <br /> запуск <BlueBG>вашей рекламы</BlueBG> <br />
          со знакомства{" "}
          <PinkBG>
            <svg
              width="64"
              height="40"
              viewBox="0 0 64 40"
              fill="none"
              xmlns="http://www.w3.org/2000/svg"
            >
              <path
                d="M0 0C13 25.999 34 38.999 64 40H0V0Z"
                fill="url(#paint0_linear_215_2)"
              />
              <defs>
                <linearGradient
                  id="paint0_linear_215_2"
                  x1="0"
                  y1="0"
                  x2="28"
                  y2="40"
                  gradientUnits="userSpaceOnUse"
                >
                  <stop stopColor="#D85599" />
                  <stop offset="1" stopColor="#D64B93" />
                </linearGradient>
              </defs>
            </svg>
            и чата
          </PinkBG>
        </Title>

        <Text size="20">
          Предложим вам лучшее решение, <br />
          которое принесет прибыль
        </Text>

        <Button>Начать общение</Button>
      </InnerWrap>

      <FooterNav>
        <InnerWrap>
          <LogoImg src={Logo_desktop} loading="lazy" alt="SocialStream" />

          <Nav>
            <a href="#">Технология</a>
            <a href="#">Кейсы</a>
            <a href="#">Компания</a>
            <a href="#">Контакты</a>
            <a href="#">Биржа Telegram</a>
            <a href="#">Блог</a>
            <a href="#">Паблишерам</a>
          </Nav>

          <Contacts>
            <Text>ООО «СошелДжет рус»</Text>
            <Text>
              123001, г. Москва, <br /> ул. Большая Садовая, дом 5, <br />{" "}
              корпус 1, офис 515
            </Text>
            <a href="tel:74957685865">+7 (495) 768 58 65</a>
            <Email href="mailto:partners@socialjet.pro">
              partners@socialjet.pro
            </Email>
          </Contacts>

          <Socials>
            <LazyLoadImage src={telegram} alt="telegram" loading="lazy" />
            <LazyLoadImage src={vk} alt="vk" loading="lazy" />
            <LazyLoadImage src={instagram} alt="instagram" loading="lazy" />
            <LazyLoadImage
              src={odnoklassniki}
              alt="odnoklassniki"
              loading="lazy"
            />
            <LazyLoadImage src={yandexzen} alt="yandexzen" loading="lazy" />
            <LazyLoadImage src={youtube} alt="youtube" loading="lazy" />
          </Socials>

          <Text>© 2015–2023 Socialjet</Text>

          <Policy href="#">
            Политика обработки <span>персональных данных</span>
          </Policy>
          <Policy href="#">Правила оказания услуг</Policy>
          <Policy href="#">Пользовательское соглашение</Policy>
        </InnerWrap>
      </FooterNav>
    </SFooter>
  );
};

export default Footer;
